.floatingdiv{
    justify-content: space-around;
    display: flex;
    background: white;
    box-shadow: var(--boxShadow);
    border-radius: 17px;
    align-items: center;
    padding: 0px 28px 0px 0px;
    height: 4.5rem;
}

.floatingdiv>img{
    height: 100px;
    width: 100px;
    /* transform: scale(0.6); */
    margin: 0;
}
.floatingdiv>span{
    font-family: monospace;
    font-size: 16px;
    color: black;
}