.card{
    width: 10rem;
    height: 13rem;
    position: absolute;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    width: 10rem;
    text-align: center;
    background: rgba(255, 255, 255, 0.26);
    border: 7px solid var(--orangeCard);
    box-shadow: var(--boxShadow);
    border-radius: 20px;
    padding: 0px 26px 2rem 26px;
}
.card span:nth-of-type(2)
{
    color: var(--gray);
    font-size: 16px;
}
.card > img
{
    height: 100px;
    width: 100px;
    transform: scale(0.9);
    margin-top: .1rem;
    border-radius: .5rem;
    box-shadow:rgba(255, 255, 255, 0.26); ;
}
.c-button{
    background: #ffffff;
    box-shadow: 0px 19px 60px rgba(0, 0, 0, 0.08);
    border-radius: 7px;
    border:none;
    padding: 10px;
    font-style: 16px;
    font-weight: 400;
    color: #5290FD;
}
@media screen and (max-width: 480px) {
    .card{
        align-items: center;
        width: auto;
    }
}